.bannerHome{
  width: 100%;
  height: 100vh;
  overflow: hidden;
  .video-container{
    height: 100%;
    width: 100vh;
    position: relative;
  }
  video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    object-position: center;
    object-fit: cover;
  }
}